<template>
  <div class="login">
    <header>
      <div class="register" @click="changepage({name:'register'})">注册</div>
    </header>
    <div class="loginbox">
      <div class="title">密码登录</div>
      <div class="form-box">
        <div class="input phoneinp">
          <input class="inptext" type="text" placeholder="请输入手机号码" v-model="user.phone" />
        </div>

        <div class="input pwdinp">
          <input class="inptext" type="password" placeholder="密码长度6-20位" v-model="user.pwd" />
        </div>

        <div class="login-btn" @click="login">登录</div>
        <div class="subtitle" @click="changepage({name: 'login'})">快捷登录</div>

      </div>
      <div class="third-part">
        <div class="subtitle"> ——————— 第三方登录 ———————</div>
        <div class="icon clearfix">
          <span>
            <i class="wechat"></i>
          </span>
          <span>
            <i class="weibo"></i>
          </span>
          <span>
            <i class="qq"></i>
          </span>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
// 同理后台登录 在客户端注册时生成 生成登录验证token 并在register.vue注册成功时保存token
  import{valid} from '../assets/js/valid'  // 导入valid.js

  export default {
    name:'pwdlogin',

    data(){
      return{
        // 确保this.axios({})关联成功，定义对象 —— 绑定在html上 _ ajax中传入params
        user:{
          phone:'', //手机号19849042805
          pwd: ''
        },
      }
    },

    methods:{

      //登录
      login(){
        this.$toast({
          duration: 0,
          message: '登录中...'
        });

        //验证手机号
        if(!valid.isPhone(this.user.phone) || !valid.isPwd(this.user.pwd)){
          // 加载
          this.$notify({
            type: 'warning',
            message: '手机号码或密码不正确'
          });
          return;
        }

        this.axios({
          method: 'POST',
          url: '/apppwdLogin', //路径设置放在main.js中
          
          //post请求参数
          // 登录方式loginType ==> 0: 手机号+验证码登录，1：手机号+密码登录
          data: {
            phone: this.user.phone,
            pwd: this.user.pwd,
            loginType:1
          }
        }).then(result =>{
          // console.log('apppwdLogin result =>',result); 

          this.$toast.clear(); //清除加载提示

          // 登录验证token
          if (result.data.status == 2000) {
            this.$cookies.set(result.data.__utk.key, result.data.__utk.utk, result.data.time * 24 * 60 * 60);
          }    

          //登录成功提示
          this.$notify({
            type: result.data.status == 2000 ? 'success' : 'warning',
            message: result.data.msg,

            //当前关闭提示后跳转
            onClose: () => {
              if (result.data.status == 2000) {
                this.$router.push({name: 'home'});
              }
            }
          });

        }).catch( err=>{
          // console.log('登录出错');
          this.$toast.clear();
        })

      },

      // changepage
      changepage(o){
        this.$router.push(o);
      }
    }
  }
</script>

